#shop{
	background-color: #161616;
	background-image: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23c79d4b' fill-opacity='0.28'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
	display:flex;
	flex-direction:column;
	align-items:center;
	padding: 3vh;
}

#shop h2{
	font-family: Nunito,sans-serif;
}

#container{
	display:flex;
	justify-content: center;
	align-items:flex-end;
	flex-wrap:wrap;
	gap:10vh;
	padding: 10vh 2vh;
	color:black;
}

.item{
	width:35vh;
	display:flex;
	flex-direction:column;
	align-items:center;
	justify-content: flex-end;
	background-color: #111111;
	color:white;
	box-shadow: 0.7vh 0.7vh black;
	border-radius: 2vh;
	padding:2vh;
	gap:2vh;
}

.item .price{
	color:#c79d4b;
}
.item img{
	width: 100%;
}

.count-handler{
	display:flex;
	justify-content:space-between;
	align-self: flex-end;
	width:100%;
}

.cart-icon{
	display:flex;
}

.item-indicator{
	font-size:2.5vh
}